import React, { useReducer } from 'react'
import proyectoReducer from './proyectoReducer'
import { proyectoContext } from "./proyectoContext"


const ProyectoState = props => {

    const iniState={
        formulario:false,
        listadoProyectos:[],
        proyectoSeleccionado:null
    }

    const [state,dispatch]=useReducer(proyectoReducer,iniState);

    const cambiarFormulario=()=>{
        dispatch({
            type:"toogle"
        })
    }

    const agregarProyecto=nombre=>{
        dispatch({
            type:"add",
            payload:{nombre,id:nombre}
        })
    }

    const obtenerProyecto=proyectoID=>{
        dispatch({
            type:"obtenerProyecto",
            payload:proyectoID
        })
    }

    const eliminarProyecto=proyectoID=>{
        dispatch({
            type:"eliminarProyecto",
            payload:proyectoID
        })
    }



    return (
        <proyectoContext.Provider
            value={{
                formulario:state.formulario,
                cambiarFormulario,
                listadoProyectos:state.listadoProyectos,
                agregarProyecto,
                proyectoSeleccionado:state.proyectoSeleccionado,
                obtenerProyecto,
                eliminarProyecto,
            }}
        >
            {props.children}
        </proyectoContext.Provider>
    )
}

export default ProyectoState
