const tareaReducer=(state,action)=>{
    switch (action.type) {

        case "agregar":
            return {
                ...state,
                tareas:[action.payload, ...state.tareas]
            };

        case "eliminar":
            return {
                ...state,
                tareas:state.tareas.filter(tarea=>tarea.tareaID!==action.payload)
            };
        
        case "cambiar":
            return {
                ...state,
                tareas:state.tareas.map(tarea=>
                    tarea.tareaID===action.payload.tareaID
                    ?{...tarea,estado:!tarea.estado}
                    :tarea
                )
            };

        case "editar":
            return {...state,tareas:state.tareas.map(tarea=>tarea.tareaID===action.payload.tareaID?action.payload:tarea)}
        default:
           return state;
    }
}

export default tareaReducer;