import React, { useContext, useState } from 'react'
import { proyectoContext } from '../../context/proyectos/proyectoContext';

const NuevoProyecto = () => {
    const {formulario,cambiarFormulario,agregarProyecto}=useContext(proyectoContext);
    
    const [error,setError]=useState(false);
    const [proyecto,setProyecto]=useState({
        nombre:''
    });



    const change=e=>{
        setProyecto({
            ...proyecto,
            [e.target.name]:e.target.value
        });
    }

    const submit=e=>{
        e.preventDefault();
        if(!proyecto.nombre.trim()){
            setError(true);
            return;
        }
        setError(false);
        cambiarFormulario();
        agregarProyecto(proyecto.nombre);
    }

    return (
        <>
            <button 
                type="button"
                className="btn btn-block btn-primario"
                onClick={cambiarFormulario}
                disabled={formulario}
            >Nuevo Proyecto</button>

            {formulario?
                (
                    <form 
                        className="formulario-nuevo-proyecto"
                        onSubmit={submit}
                        autoComplete="off"
                    >
                        <input
                            type="text"
                            className="input-text"
                            placeholder="Nombre Proyecto"
                            name="nombre"
                            onChange={change}
                        />

                        <input 
                            type="submit"
                            value="Agregar Proyecto"
                            className="btn btn-block btn-primario"
                        />
                        {error?<p className="mensaje error">El nombre es obligatio</p>:null}
                    </form>
                )
            :null}
            

        </>
    )
}

export default NuevoProyecto
