import React, {useContext, useState} from 'react'
import { proyectoContext } from '../../context/proyectos/proyectoContext';
import tareaContext from '../../context/tareas/tareaContext';

const FormTarea = () => {
    const {proyectoSeleccionado}=useContext(proyectoContext);
    const {agregarTarea,editar,nombre,setNombre,editarTarea,setEditar}=useContext(tareaContext);
    const [error,setError]=useState(false);

    if(!proyectoSeleccionado)return null;

    const {id}=proyectoSeleccionado[0];

    const change=e=>{
        setNombre(e.target.value);
    }

    const validarTarea=e=>{
        e.preventDefault();
        if(!nombre.trim()){
            setError(true);
            return;
        }
            setError(false);
        if(editar===null){
            const tarea={
                nombre,
                estado:false,
                proyectoID:id,
                tareaID:nombre
            }
            agregarTarea(tarea);
        }

        else{
            editarTarea({
                ...editar,
                nombre
            })
            setEditar(null);
        }
        setNombre("");
        
    }


    return (
        <div className="formulario">
            <form
                onSubmit={validarTarea}
                autoComplete="off"
            >
                <div className="contenedor-input">
                    <input
                        type="text"
                        className="input-text"
                        placeholder="Nombre Tarea..."
                        name="nombre"
                        onChange={change}
                        value={nombre}
                    />
                </div>

                <div className="contenedor-input">
                    <input
                        type="submit"
                        className="btn btn-primario btn-submit btn-block"
                        value={editar?"Editar tarea":"Agregar tarea"}
                    />
                </div>
                {error?<p className="mensaje error">El nombre de la tarea es obligatorio</p>:null}
            </form>
        </div>
        
    )
}

export default FormTarea
