import React, { useContext } from 'react'
import { proyectoContext } from '../../context/proyectos/proyectoContext'
import tareaContext from '../../context/tareas/tareaContext';
import Tarea from './Tarea'

const ListadoTareas = () => {
    const {proyectoSeleccionado,eliminarProyecto}=useContext(proyectoContext);
    const {tareas}=useContext(tareaContext);

    if(!proyectoSeleccionado)return <h2>Selecciona un proyecto</h2>;
    const {nombre,id}=proyectoSeleccionado[0];

    const tareasFiltradas=tareas.filter(tarea=>tarea.proyectoID===id)

    return (
        <>
            <h2>Proyecto: {nombre}</h2>  
            <ul className="listado-tareas">

                {tareasFiltradas.length===0
                ?<li className="tarea"><p>No hay tareas</p></li>
                :tareasFiltradas.map((tarea,index)=>(
                    <Tarea tarea={tarea} key={index} />
                ))}

            </ul>

            <button 
                className="btn btn-eliminar"
                onClick={()=>eliminarProyecto(id)}
            >Eliminar Proyecto &times;</button>
        </>
    )
}

export default ListadoTareas
