import React, { useContext } from 'react';
import { Redirect, BrowserRouter as Router, Switch } from 'react-router-dom';
import Login from '../compents/auth/Login';
import NuevaCuenta from '../compents/auth/NuevaCuenta';
import Proyectos from '../compents/proyectos/Proyectos';
import authContext from '../context/auth/authContext';
import PrivateRoute from './PrivateRoute';
import PublicRouter from './PublicRouter';

const AppRouter = () => {
    const {nombre}=useContext(authContext);
    console.log(nombre);
    return (
        <Router>
            <Switch>
                <PublicRouter
                    exact 
                    path="/" 
                    component={Login} 
                    userName={nombre}
                />
                <PublicRouter
                    exact 
                    path="/nueva-cuenta" 
                    component={NuevaCuenta} 
                    userName={nombre}
                />
                <PrivateRoute 
                    exact 
                    path="/proyectos" 
                    component={Proyectos} 
                    userName={nombre}
                />
                <Redirect to="/404" />
            </Switch>
        </Router>
    )
}

export default AppRouter;
