
const proyectoReducer=(state,action)=>{
    switch (action.type) {
        case "toogle":
            return {
                ...state,
                formulario:!state.formulario
            };

        case "add":
            return {
                ...state,
                listadoProyectos:[action.payload, ...state.listadoProyectos]
            };

        case "obtenerProyecto":
            return {
                ...state,
                proyectoSeleccionado:state.listadoProyectos.filter(proyecto=>proyecto.id===action.payload)
            };

        case "eliminarProyecto":
            return {
                ...state,
                listadoProyectos:state.listadoProyectos.filter(proyecto=>proyecto.id!==action.payload),
                proyectoSeleccionado:null
            };

        case "login":
            return action.payload;
        
        default:
            return state;
    }
}

export default proyectoReducer;